<template lang="pug">
q-btn.quan-btn(:class='btnClasses')
</template>

<script lang="ts">
import { computed, defineComponent } from 'vue';

export default defineComponent({
  props: {
    size: {
      type: String,
      default: 'md',
    },
  },

  setup(props) {
    const btnClasses = computed(() => {
      return {
        [`quan-btn--size-${props.size}`]: props.size,
      };
    });

    return { btnClasses };
  },
});
</script>

